import axios from "axios";

//
export function sw_log_logs(queryString) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "GET",
    url: `https://13.124.69.255:90/api/sw_log/GetLog_List?${queryString}`,
    mode: "no-cors",
  });
}
